@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
/* basic
   ===================================== */

html {
  font-size: 100%;
  width: 100%;
  height: 100%;
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Roboto', Helvetica, "Helvetica Neue", Arial, sans-serif;
  font-size: 16px;
  line-height: 1.52947;
  font-weight: normal;
  -webkit-font-smoothing: subpixel-antialiased;
  -moz-osx-font-smoothing: auto;
  direction: ltr;
  color: #323232;
  background-color: #f2f2f2;
  height: 100%;
  margin: 0;
}

#root {
  overflow-x: hidden;
  height: 100%;
}

img {
  display: inline-block;
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

.modal-mask {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0,0,0,.8);
	z-index: 5000;
}

.modal {
	position: absolute;
	top: 50px;
	left: 50%;
	text-align: center;
	background: #fff;
	width: 335px;
	margin: auto;
	border-radius: 4px;
	padding: 35px;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
	cursor: auto;
}

.crop-title {
	display: block;
	font-size: 14px;
	color: #8e8e8e;
	margin: 12px 0;
}

.crop-controls {
	margin-top: 25px;
}

.crop-controls .crop-control:first-child {
	margin-right: 15px;
}

.crop-control {
	position: relative;
	font-size: 14px;
	color: #232323;
	text-transform: uppercase;
	background: #fff;
	border: 0;
	border-radius: 4px;
	padding: 10px 25px;
	cursor: pointer;
	-webkit-transition: background 0.15s ease-in, color 0.15s ease-in;
	transition: background 0.15s ease-in, color 0.15s ease-in;
	box-shadow: 0 0 25px 1px rgba(0,0,0,0.1);
}

.crop-control:hover {
	background: #dadada;
}

.css-1ik466l-Slider .css-z9sd19-Slider {
	background-color: #525252;
}

.crop-loading {
	position: absolute;
	left: -5px;
}

.popup-enter-active {
  -webkit-animation: IGCoreModalShow 300ms ease-in;
          animation: IGCoreModalShow 300ms ease-in;
}

@-webkit-keyframes IGCoreModalShow {
	0% {
		-webkit-transform: scale(1.2);
		        transform: scale(1.2);
	}

	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}

@keyframes IGCoreModalShow {
	0% {
		-webkit-transform: scale(1.2);
		        transform: scale(1.2);
	}

	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}
.map {
    height: calc(100vh - 65px); 
}

.leaflet-marker-pane {
    -webkit-animation: pulsate 1s ease-in-out 3;
            animation: pulsate 1s ease-in-out 3;
    -webkit-animation-direction: alternate, reverse, normal;
            animation-direction: alternate, reverse, normal;
}

@-webkit-keyframes pulsate {
    from {
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);
    }
    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes pulsate {
    from {
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);
    }
    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}
.sidebar {
	background: #fff;
	height: 100vh;
	margin-top: -65px;
}

.search {
	position: relative;
	margin: 15px 0 8px 0;
	padding: 0 15px;
}

.input-search {
	width: 100%;
	height: 40px;
	padding: 0 15px;
	border: 0;
	border-radius: 8px;
	box-shadow: 0 0 8px 1px rgba(0,0,0,0.1);
}

.icon-magnifier {
	position: absolute;
	top: 12px;
	right: 30px;
	background: url(/static/media/search.864b1806.svg) no-repeat center/contain;
	display: block;
	width: 18px;
	height: 18px;
}

.country_list {
	height: calc(100vh - 75px);
}

.item {
	color: #323232;
	display: flex;
	justify-content: space-between;
	padding: 6px 25px;
}

.item-country {
	-webkit-transition: -webkit-transform 0.15s ease-in;
	transition: -webkit-transform 0.15s ease-in;
	transition: transform 0.15s ease-in;
	transition: transform 0.15s ease-in, -webkit-transform 0.15s ease-in;
}

.button-add {
	background: transparent;
	border: 0;
	cursor: pointer;
}

.button-add:hover .icon-add {
	opacity: 0.7;
}

.icon-add {
	background: url(/static/media/plus.83fc3e79.svg) no-repeat center/contain;
	display: block;
	width: 18px;
	height: 18px;
	-webkit-transition: opacyti 0.15s ease-in;
	transition: opacyti 0.15s ease-in;
}
.dashboard-container {
	display: flex;
	flex-direction: column;
	align-items: flex-end;
}

.layout {
  display: flex;
  flex-direction: row-reverse;
  width: 100%;
}

.main-content {
  width: calc(100% - 300px);
}

.sidebar {
  width: 300px;
}

.dashboard {
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 55px;
  width: calc(100% - 300px);
}

.dashboard-title {
  font-size: 22px;
  color: #4e4e4e;
}

.profile {
  display: flex;
  align-items: center;
}

.upload-file {
  background: transparent;
  display: block;
  width: 45px;
  height: 45px;
  border: 0;
  border-radius: 50%;
  padding: 0;
  outline: none;
  overflow: hidden;
  cursor: pointer;
}

.profile-avatar {
  position: relative;
}

.profile-avatar::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  opacity: 0;
  -webkit-transition: opacity 0.15s ease-in;
  transition: opacity 0.15s ease-in;
}

.profile-avatar-label {
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 12px;
  color: #fff;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  opacity: 0;
  -webkit-transition: opacity 0.15s ease-in;
  transition: opacity 0.15s ease-in;
}

.profile-avatar:hover.profile-avatar::before {
  opacity: 1;
}

.profile-avatar:hover .profile-avatar-label {
  opacity: 1;
}

.input-file {
  display: none!important;
}

.btn-logout {
  font-size: 14px;
  max-width: 100%;
  white-space: nowrap;
  vertical-align: middle;
  background: #41A6F1;
  color: rgb(255, 255, 255);
  height: 40px;
  line-height: 40px;
  border-width: 0;
  text-decoration: none;
  border-radius: 35px;
  padding: 0 25px;
  margin-left: 25px;
  -webkit-transition: background 0.1s ease-out 0s, box-shadow 0.15s cubic-bezier(0.47, 0.03, 0.49, 1.38) 0s;
  transition: background 0.1s ease-out 0s, box-shadow 0.15s cubic-bezier(0.47, 0.03, 0.49, 1.38) 0s;
  outline: none;
  cursor: pointer;
}

.btn-logout:hover {
  background: #75c4ff;
}

.upload-photos {
  background: #fff;
  display: flex;
  align-items: center;
  padding: 8px 25px;
  margin-right: 25px;
  border: 0;
  border-radius: 8px;
  box-shadow: 0 0 6px 1px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  -webkit-transition: background 0.15s ease-in;
  transition: background 0.15s ease-in;
}

.upload-photos:hover {
  background: #dadada;
}

.upload-icon {
  background: url(/static/media/folder-upload.0c1829e5.svg) no-repeat center/contain;
  display: inline-block;
  width: 28px;
  height: 28px;
  margin-right: 15px;
}

.upload-photos-label {
  font-size: 14px;
  font-weight: 600;
  color: #565656;
}

.upload-loading {
  background: url(/static/media/loader.22043e8c.gif) no-repeat center/contain;
  display: inline-block;
  width: 18px;
  height: 18px;
  margin-left: 10px;
}

.auth-socials {
  position: relative;
  display: flex;
  justify-content: center;
  margin-bottom: 25px;
}

.auth-socials button:first-child {
  margin-right: 15px;
}

.auth-social-button {
  display: flex;
  align-items: center;
  color: #fff;
  width: auto;
  padding: 2px;
  background: transparent;
  border: 0;
  border-radius: 50%;
  -webkit-transition: background-color 0.15s;
  transition: background-color 0.15s;
  cursor: pointer;
  box-shadow: 0 0 8px 1px rgba(82, 82, 82, 0.1);
  -webkit-transition: -webkit-transform 0.15s ease-in;
  transition: -webkit-transform 0.15s ease-in;
  transition: transform 0.15s ease-in;
  transition: transform 0.15s ease-in, -webkit-transform 0.15s ease-in;
}

.auth-social-button:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.icon-gpl {
  background: url(/static/media/g-pl.695a3160.svg) no-repeat center/18px;
  display: inline-block;
  width: 35px;
  height: 35px;
}

.icon-fb {
  background: url(/static/media/fb.346b3e40.svg) no-repeat center/18px;
  display: inline-block;
  width: 35px;
  height: 35px;
}
/*
Auth
=============================
*/

.container-auth {
  display: flex;
  justify-content: center;
  min-height: 545px;
}

@media screen and (min-width:320px) and (max-width: 420px) {
  .container-auth {
    min-height: auto;
    height: 100vh;
  }
}

/*
Auth-information
=============================
*/

.auth-information {
  background: rgba(65, 166, 241, 1);
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 15px 75px;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  max-width: 355px;
}

@media screen and (min-width: 320px) and (max-width: 420px) {
  .auth-information {
    display: none;
  }
}

.auth-information-heading {
  font-size: 28px;
  font-weight: 500;
  color: #fff;
}

.auth-information-descr {
  font-size: 14px;
  font-weight: 300;
  color: #fff;
  margin-top: 10px;
}

.button-view {
  font-family: inherit;
  font-size: 14px;
  font-weight: 300;
  height: 45px;
  line-height: 45px;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  color: #fff;
  background: transparent;
  display: inline-block;
  border: 1px solid #fff;
  border-radius: 32px;
  width: 175px;
  margin-top: 25px;
  -webkit-transition: background 0.15s ease-in, color 0.15s ease-in;
  transition: background 0.15s ease-in, color 0.15s ease-in;
}

.button-view:hover {
  background: #fff;
  color: #333;
}

/*
Auth-form
=============================
*/

.wrapper-auth {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 475px;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 55px 55px 75px 55px;
}

@media screen and (min-width: 320px) and (max-width: 420px) {
  .wrapper-auth {
    width: 100%;
    padding: 55px 6% 75px 6%;
  }
}

.auth-title {
	font-size: 28px;
	font-weight: 500;
	color: #cccccc;
	text-align: center;
	margin-bottom: 25px;
}

.auth-or {
  font-size: 14px;
  text-align: center;
  color: #989898;
  display: block;
  margin-bottom: 12px;
}

.auth-field {
  position: relative;
  margin-bottom: 20px;
}

.field {
  width: 100%;
  padding: 15px 20px;
  border: 1px solid #f2f2f2;
  border-radius: 30px;
  background: #FCFBFC;
  outline: none;
}

.eye {
  position: absolute;
  top: 13px;
  right: 15px;
  background: url(/static/media/eye.b2682600.svg) no-repeat center/contain;
  display: block;
  width: 20px;
  height: 20px;
  opacity: 0.4;
  cursor: pointer;
  -webkit-transition: opacity 0.15s ease-in;
  transition: opacity 0.15s ease-in;
}

.field:focus {
  border-color: #41A6F1;
  background: #fff;
}

.field.is-error {
  border-color: rgb(222, 53, 11);
}

.error-server {
  font-size: 12px;
  color: rgb(222, 53, 11);
  text-align: center;
  margin-top: 15px;
}

.auth-button {
  margin-top: 25px;
  text-align: center;
}

.button-send {
  display: inline-flex;
  justify-content: center;
  font-size: 14px;
  font-weight: 300;
  text-transform: uppercase;
  max-width: 100%;
  white-space: nowrap;
  vertical-align: middle;
  width: 175px;
  color: rgb(255, 255, 255);
  height: 45px;
  line-height: 45px;
  border-width: 0;
  text-decoration: none;
  border-radius: 30px;
  padding: 0 8px;
  -webkit-transition: background 0.1s ease-out 0s, box-shadow 0.15s cubic-bezier(0.47, 0.03, 0.49, 1.38) 0s;
  transition: background 0.1s ease-out 0s, box-shadow 0.15s cubic-bezier(0.47, 0.03, 0.49, 1.38) 0s;
  outline: none;
}

.button-send:enabled {
  background: #41A6F1;
  cursor: pointer;
}

.button-send:disabled {
  background-color: #cecece;
  cursor: default;
}

.button-send:enabled.button-send:hover {
  background: #75c4ff;
}

@media screen and (min-width: 320px) and (max-width: 420px) {
  .button-send {
    width: 100%;
    height: 50px;
    line-height: 50px;
  }
}

.field-valid {
  position: absolute;
  top: calc(50% - (15px / 2));
  left: -25px;
  display: block;
  width: 15px;
  height: 15px;
  background: url(/static/media/checked.6e5e5bc6.svg) no-repeat center/contain;
}

@media screen and (min-width: 320px) and (max-width: 420px) {
  .field-valid {
    left: -6%;
  }
}

.auth-bottom {
  font-size: 14px;
  color: #989898;
  text-align: center;
  margin-top: 25px;
}

@media screen and (min-width: 420px) {
  .auth-bottom{
    display: none;
  }
}

.auth-link {
  color: #4292e7;
  text-decoration: none;
}

.text-error {
  font-size: 12px;
  color: rgb(222, 53, 11);
  position: absolute;
  left: 20px;
  bottom: -20px;
  display: block;
}

/*
Auth-animate route
=============================
*/

.example-enter {
  opacity: 0;
}

.example-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

.example-exit {
  opacity: 0;
}

.example-exit-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

/*
Auth-animate error message
=============================
*/
.alert-enter {
  opacity: 0;
}
.alert-enter-active {
  opacity: 1;
  -webkit-transition: opacity 300ms;
  transition: opacity 300ms;
}
.alert-exit {
  opacity: 1;
}
.alert-exit-active {
  opacity: 0;
  -webkit-transition: opacity 300ms;
  transition: opacity 300ms;
}
.main-container {
  min-height: 100%;
}

.app-auth {
  background: url(/static/media/main-bg.c06c9419.jpg) no-repeat top/cover;
  padding-top: 85px;
}

@media screen and (min-width: 320px) and (max-width: 420px) {
  .app-auth {
    padding: 0;
  }
}

.app-dashboard {
  background: none;
}

